import React, { useState, useRef, useContext } from "react";

import { Link } from "react-scroll";
import styled from "styled-components";
import { ThemeContext } from "../../ThemeContext/ThemeContext";
import resume from "../../Assests/Resume/Amit_Bhutani_Resume.pdf";
import styles from "./Navbar.module.css";
import { FaBeer } from 'react-icons/fa'
import { BsFillEmojiSunglassesFill } from 'react-icons/bs'
import {  RiMoonClearFill } from 'react-icons/ri'


const barStyle = {
  bar1: {
    position: "absolute",
    width: "15px",
    transform: "rotate(45deg)",
  },
  bar2: {
    position: "absolute",
    left: "-2px",
  },
  bar3: {
    position: "absolute",
    width: "15px",
    transform: "rotate(-45deg)",
  },
};

const Navbar = () => {

  const navRef = useRef();

  const { newTheme, mode, handleMode, open, handleMenu } =
    useContext(ThemeContext);



  return (
    <nav
      ref={navRef}
      className={styles.navContainer}
      style={{
        background: `${newTheme.background}`,
        boxShadow: `3px 3px 10px ${newTheme.line}`,
      }}
    >
      <div className={styles.navbar}>
        <a href="">
          <div className={styles.logo}>
            <img
              src="https://thumbs.dreamstime.com/b/ab-logo-concept-isolated-white-background-modern-design-style-web-site-mobile-app-ab-logo-concept-isolated-white-169703318.jpg"
              alt="Logo"
            />
          </div>
        </a>

        <div style={{ color: `${newTheme.title}` }} className={styles.links}>
          <Link
            to="home"
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-200}
            duration={500}
          >
            Home
          </Link>
          <Link
            to="about"
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-100}
            duration={500}
          >
            About
          </Link>
          <Link
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
            to="techstacks"
          >
            Skills
          </Link>
          <Link
            to="projects"
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            Projects
          </Link>

          <Link
            to="contact"
            spy={true}
            smooth={true}
            offset={-100}
            duration={500}
          >
            Contact
          </Link>
          <a
            href={resume}
            download={"Amit_Bhutani_Resume"}
            target="_blank"
            rel="noreferrer"
            
          >
            Resume
          </a>
        </div>
        <button
          style={{ color: `${newTheme.title}` }}
          className={styles.modeButton}
          onClick={handleMode}
        >
        
          {mode === "light" ? (
           
            // <i class="fas fa-lightbulb"></i>
            <BsFillEmojiSunglassesFill style={{color : "#D35400"}}/>
          ) : (
            <RiMoonClearFill style={{color : "#F1C40F"}}/>
          )}
        </button>

        <div onClick={handleMenu} className={styles.bars}>
          <div
            style={open ? { background: `${newTheme.title}` } : barStyle.bar1}
          ></div>
          <div
            style={open ? { background: `${newTheme.title}` } : barStyle.bar2}
          ></div>
          <div
            style={open ? { background: `${newTheme.title}` } : barStyle.bar3}
          ></div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;

const Home = styled.div``;
